.templateradio {
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 11;
  -webkit-appearance: none;
  -moz-appearance: none;
  background: #fff;
  border: 2px solid #e2ebf6;
  border-radius: 50%;
  cursor: pointer;
  height: 1.5em;
  outline: none;
  transition: 
    background 0.2s ease-out,
    border-color 0.2s ease-out;
  width: 1.5em; 
  
}
.templateradio::after {
  border: 2px solid #fff;
  border-top: 0;
  border-left: 0;
  content: '';
  display: block;
  height: 0.75rem;
  left: 25%;
  position: absolute;
  top: 50%;
  transform: 
    rotate(45deg)
    translate(-50%, -50%);
  width: 0.375rem;
}
.templateradio:checked {
  background: #45deef;
  border-color: #45deef;
}
