:root {
  --white: #fff;
  --orange: #f36c00;
}

.outer-container {
  max-width: 800px;
  margin: auto;
  display: flex;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  /* height: calc(100vh - 220px); */ /* extra */
}

.outer-container > div {
  flex: 1;
  padding: 0px; /* extra */
}

.outer-container .carousel-wrapper {
  background: var(--orange);
  display: flex;
  flex-direction: column;
}

.outer-container .carousel-wrapper p,
.carousel-item-2 {
  font-weight: 700;
  font-size: 20px;
  color: var(--white);
  line-height: 1.3;
}

.outer-container .content {
  background: var(--white);
}

.leading-text {
  display: flex;
  align-items: center;
  height: 100%;
  margin-right: 6px;
}

.carousel {
  height: 100%;
  display: flex;
  flex: 15; 
  align-items: center;
  color: var(--white);
}

.carousel-button {
  display: flex;
  align-self: center;
  padding: 10px;
  max-width: 50px;
  border-radius: 30px;
  background: var(--orange);
  border: 2px solid var(--white);
}

.carousel-button svg {
  height: 26px;
  width: 26px;
}
.carousel-button path {
  fill: var(--white);
}

.slides {
  align-self: flex-start;
  flex: 1;
  width: 100%;
  overflow: hidden;
}

.carousel-inner { /* extra */
  position: relative;
  /* max-height: 250px;
  height: 250px; */ 
  display:flex;
  height: calc(100vh - 420px);
}

.carousel-inner::before,
.carousel-inner::after {
  content: "";
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 110px;
  pointer-events: none;
}

.carousel-inner::before {
  top: 0;
  background: linear-gradient(
    0deg,
    hsla(0, 0%, 100%, 0) 0%,
    hsla(27, 100%, 48%, 1) 100%
  );
}

.carousel-inner::after {
  bottom: 0;
  background: linear-gradient(
    0deg,
    hsla(27, 100%, 48%, 1) 0%,
    hsla(0, 0%, 100%, 0) 100%
  );
}

.carousel-item-2 {
  position: absolute;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  opacity: 0;
  top: 100px;  /* extra */
  transition: transform 0.4s ease, opacity 0.4s ease;
}

.carousel-item-2.visible {
  opacity: 1;
}


/* extra others */
.flex-item {
  height: 2.7rem;
  width: 8rem;
  padding: 0 1.2rem;
  flex: 1 1 33.33333%;
  white-space: nowrap;
  max-width: 50%;
}


.del {
  position: relative;
  top: 38px;
  left: 0;
  margin-top: -31px;
  margin-left: 43px;
  cursor: pointer;
  max-width: 7px;

	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
	outline: none;
	cursor: pointer;
	padding: 10px;
	overflow: hidden;
	border: none;
	-webkit-border-radius: 50%;
	border-radius: 50%;
	color: rgba(255,255,255,0.9);
	text-align: center;
	background: #1abc9c;
	-webkit-box-shadow: 0 4px 3px 2px rgba(0,0,0,0.2) ;
	box-shadow: 0 4px 3px 2px rgba(0,0,0,0.2) ;
}

.del:active {
	opacity: 0.85;
	-webkit-box-shadow: 2px 2px 2px 0 rgba(0,0,0,0.2) ;
	box-shadow: 2px 2px 2px 0 rgba(0,0,0,0.2) ;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
